import { Component, OnInit } from '@angular/core';
import { RestaurangServiceService} from '../restaurang-service.service';
import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-kontakter',
  templateUrl: './kontakter.component.html',
  styleUrls: ['./kontakter.component.css']
})
export class KontakterComponent implements OnInit {

  restaurangId: Number;
  bokningsInfo: any;

  constructor(private activatedRoute: ActivatedRoute, private restaurangService: RestaurangServiceService ) { }

  ngOnInit() {
      this.restaurangId= Number(this.activatedRoute.snapshot.paramMap.get('id'));
      console.log(this.restaurangId);
      
      this.restaurangService.getRestaurang().subscribe((data)=> {
      console.log(data);
      this.bokningsInfo = data; 
  });

}
   //logout(){
    //this.restaurangService.logout();
    //this.activatedRoute.navigateByUrl('login');
//}
}
