import { Component, OnInit } from '@angular/core';
import { RestaurangServiceService} from '../restaurang-service.service';
import { FormBuilder, FormGroup, Validators } from  '@angular/forms';
import { Router } from  '@angular/router';
import { User } from  '../user';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  constructor(private restaurangService: RestaurangServiceService, private router: Router, private formBuilder: FormBuilder) { }
  loginForm: FormGroup;
  isSubmitted  =  false;
 
   ngOnInit() {
      this.loginForm  =  this.formBuilder.group({
        email: ['Burgers.com', Validators.required],
        password: ['hello', Validators.required]
    });
  }
    get formControls() { return this.loginForm.controls; }
    
   login(){
    console.log(this.loginForm.value);
    this.isSubmitted = true;
    if(this.loginForm.invalid){
      return;
    }
    this.restaurangService.login(this.loginForm.value);
    this.router.navigateByUrl('/list');

}
}